.particles-js-canvas-el {
  position: absolute; }

#logo {
  width: 50px; }

body {
  background-color: #0d2938;
  color: white;
  font-family: Poppins; }

h1 {
  font-size: 45px;
  font-weight: 100; }

span.orange {
  color: #f77f00; }

.nav {
  position: fixed;
  width: 100vw;
  display: flex;
  justify-content: space-between;
  background-color: #05222e;
  z-index: 1; }
  .nav__logo {
    display: flex;
    align-items: center; }
  .nav__text {
    font-size: 24px;
    margin-left: 20px; }
  .nav__buttons {
    display: flex; }
    .nav__buttons img {
      margin: 0 20px;
      height: 30px;
      filter: invert(100%); }

.hero {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100vh;
  padding: 0 5%; }
  .hero #scroll {
    width: 5vw;
    margin: 5vw 0; }
  .hero #image {
    position: absolute;
    height: 20%;
    bottom: 0;
    transform: translate(-50%, 0); }
  .hero__moreAbout {
    text-align: center; }

.about {
  padding: 0 5%;
  display: flex;
  background-color: #05222e;
  width: 100%;
  flex-wrap: wrap; }
  .about__title {
    width: 100%;
    text-align: center; }
  .about__content {
    display: flex;
    width: 100%;
    flex-wrap: wrap;
    justify-content: space-evenly;
    align-items: center; }
    .about__content aside:first-child {
      width: 750px; }
    .about__content aside {
      width: 550px; }
  .about__stackList {
    display: flex;
    flex-wrap: wrap;
    text-transform: uppercase;
    justify-content: center; }
    .about__stackList li {
      margin: 30px 0;
      display: flex;
      flex-direction: column;
      align-items: center; }
  .about__image {
    width: 60px;
    margin: 0 20px 15px 20px; }
  .about #paragraph {
    margin: 15px 0;
    line-height: 150%; }
  .about__links {
    display: flex;
    justify-content: space-evenly; }
    .about__links #link {
      border: 1px solid white;
      color: white;
      text-decoration: none;
      padding: 3px 20px; }

.projects {
  padding: 0 5%;
  display: flex;
  flex-wrap: wrap; }
  .projects__title {
    text-align: center;
    width: 100%; }
  .projects__list {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly; }
  .projects__card {
    border: 2px solid #f77f00;
    border-radius: 10px;
    flex-basis: 350px;
    padding: 5px;
    margin: 3%; }
  .projects__cardImage {
    width: 100%;
    border-radius: 10px; }
  .projects__stack {
    display: flex;
    margin-top: 30px;
    flex-wrap: wrap; }
    .projects__stack li {
      padding: 5px;
      border: 1px solid white;
      border-radius: 10px;
      font-size: 12px;
      margin: 5px; }

.contact {
  padding: 0 5%;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  background-color: #05222e; }
  .contact__title {
    text-align: center;
    width: 100%; }
  .contact__form {
    display: flex;
    flex-direction: column;
    width: 100%;
    align-items: center; }
    @media (min-width: 600px) {
      .contact__form {
        width: 50%; } }
  .contact .contact__inputField {
    border-bottom: 2px solid #f77f00;
    background-color: #0d2938;
    padding: 10px;
    margin: 10px 0;
    width: 80%; }
    .contact .contact__inputField img {
      width: 20px; }
    .contact .contact__inputField input,
    .contact .contact__inputField textarea {
      background-color: transparent;
      border: none;
      color: white;
      resize: none;
      width: 90%;
      height: 100%; }
    .contact .contact__inputField input::placeholder,
    .contact .contact__inputField textarea::placeholder {
      color: white;
      opacity: 0.8; }
  .contact button {
    border: 1px solid #f77f00;
    background-color: transparent;
    color: white;
    width: 50%;
    padding: 15px;
    margin: 0 auto; }
  .contact__links {
    width: 100%; }
